import React from 'react';
import Navigation from '../componants/Navigation';
import Typewriter from 'typewriter-effect';

const home = () => {
    return (
        <div className="home">
            <Navigation/>
            <div className="homeContent">
                <div className="content">
                <div className="pdf">
            <a href="./media/CV.pdf" target="_blank">CV à Télécharger</a>
        </div>
            <h1><Typewriter
            options={{
                changeDeleteSpeed:40,
                autoStart: true,
                loop: true,
            }}
            onInit={(typewriter, )=>{
 
                typewriter
                .changeDelay(45)
                .changeDeleteSpeed(45)
                .typeString("Bienvenu je suis Simon Ecoffet  , <br>")
                .pauseFor(300)
                .typeString('<strong> Dev Full-Stack</strong> !')
                .pause(1000)
                .deleteChars(13)
                .typeString('<span style="color:rgb(38,77,228) ;"><strong> CSS</strong></span> !')
                .pauseFor(1000)
                .deleteChars(5)
                .typeString('<span style="color:#61DAFB;"><strong> React</strong></span> !')
                .pauseFor(1000)
                .deleteChars(7)
                .typeString('<span style="color: rgb(239,216,29);"><strong> JavaScript</strong></span> !')
                .pauseFor(1000)
                .start()
            }}
            /></h1>
            
            
                </div>
            </div>

        </div>
    );
};

export default home;