import React from 'react';
import {NavLink} from 'react-router-dom'

const Navigation = () => {
    return (
        <div className="sidebar">
            <div className="id">
                <div className="idContent">
                    <img src="./media/Simon.jpg" alt="profil-pic" width="150px" height="170px"/>
                    <h3>Simon Ecoffet</h3>
                </div>
            </div>

                <div className="navigation">
                    <ul>
                        <li>
                            <NavLink exact to="/"
                            activeClassName="navActive">
                                <i className='fas fa-home'></i>
                                <span>Acceuil</span>
                            </NavLink>
                        </li>
                        <li>
                            <NavLink exact to="/competance"
                            activeClassName="navActive">
                                <i className='fas fa-mountain'></i>
                                <span>Compétances</span>
                            </NavLink>
                        </li>
                        <li>
                            <NavLink exact to="/portefolio"
                            activeClassName="navActive">
                                <i className='fas fa-images'></i>
                                <span>Portefolio</span>
                            </NavLink>
                        </li>
                        <li>
                            <NavLink exact to="/contact"
                            activeClassName="navActive">
                                <i className='fas fa-address-book'></i>
                                <span>Contact</span>
                            </NavLink>
                        </li>
                    </ul>
                </div>

                <div className="socialNetwork">
                    <ul>
                        <li>
                            <a href="https://www.linkedin.com/in/simon-ecoffet-b17b281bb/" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin"></i></a>
                        </li>
                        <li>
                            <a href="https://github.com/esiomlink" target="_blank" rel="noopener noreferrer"><i className="fab fa-github"></i></a>
                        </li>
                        <li>
                            <a href="https://twitter.com/EcoffetSimon" target="_blank" rel="noopener noreferrer"><i className="fab fa-twitter"></i></a>
                        </li>
                        <li>
                            <a href="https://www.facebook.com/simon.ecoffet.98" target="_blank" rel="noopener noreferrer"><i className="fab fa-facebook"></i></a>
                        </li>
                    </ul>
                    <div className="signature">
                        <p>Simon Ecoffet - 2021</p>
                    </div>
                </div>
        </div>
    );
};

export default Navigation;