import React from 'react';
import Navigation from '../componants/Navigation';
import {CopyToClipboard} from 'react-copy-to-clipboard';

const contact = () => {
    return (
        <div className="contact">
            <Navigation />
            <div className="contactContent">
                <div className="header"></div>
                <div className="contactBox">
                    <h1>Contactez-moi</h1>
                    <ul>
                        <li>
                            <i className="fas fa-map-marker-alt"></i>
                            <span>Toulouse</span>
                        </li>
                        <li>
                            <i className="fas fa-mobile-alt"></i>
                            <CopyToClipboard text="0783942307">
                                <span 
                                    className="clickInput" 
                                    onClick={() =>{alert('Téléphone copié !');}}>
                                    0783942307
                                </span> 
                            </CopyToClipboard>
                        </li>
                        <li>
                            <i className="far fa-envelope"></i>
                            <CopyToClipboard text="simon.ecoffet@gmail.com">
                                <span 
                                    className="clickInput" 
                                    onClick={() =>{alert('E-mail copié !');}}>
                                    simon.ecoffet@gmail.com
                                </span> 
                            </CopyToClipboard>
                        </li>
                    </ul>
                </div>
                <div className="socialNetwork">
                <ul>
                        
                            <a href="https://www.linkedin.com/in/simon-ecoffet-b17b281bb/" target="_blank" rel="noopener noreferrer">
                                <h4>LinkedIn</h4>
                                <i className="fab fa-linkedin"></i>
                            </a>
                        
                    
                            <a href="https://github.com/esiomlink" target="_blank" rel="noopener noreferrer">
                                <h4>Github</h4>
                                <i className="fab fa-github"></i>
                            </a>
                        
                    
                            <a href="https://twitter.com/EcoffetSimon" target="_blank" rel="noopener noreferrer">
                                <h4>Twitter</h4>
                                <i className="fab fa-twitter"></i>
                            </a>
                        
                    
                            <a href="https://www.facebook.com/simon.ecoffet.98" target="_blank" rel="noopener noreferrer">
                                <h4>Facebook</h4>
                                <i className="fab fa-facebook"></i>
                            </a>
                        
                    </ul>
                </div>

            </div>
        </div>
    );
};

export default contact;