import React, { Component } from 'react';
import ProgressBar from './ProgressBar';

class Languages extends Component {
    state = {
        languages:[
            {id: 1, value:"Css", xp:2},
            {id: 2, value:"javascript", xp:1.8},
            {id: 3, value:"node.js", xp:1.2},
            {id: 4, value:"Php", xp:1},
            {id: 5, value:"Python", xp:0.5}
        ],
        frameworks:[
            {id: 1, value:"Rreact", xp:1.8},
            {id: 2, value:"Bulma", xp:1.2},
            {id: 3, value:"Sass", xp:2},
            {id: 4, value:"Bootstrap", xp:1}
        ]
    }
    render() {
        let {languages, frameworks} = this.state;

        return (
            <div className="languagesfremwork">
                <ProgressBar
                languages={languages}
                className="languagesDisplay"
                title="Languages"
                
                />
                <ProgressBar
                languages={frameworks}
                title="Frameworks & bibliotèques"
                className="frameworksDisplay"
                />
                
            </div>
        );
    }
}

export default Languages;